.nav-bar {
  display: flex;
  top: 0;
  width: 100%;
  height: 40px;
  position: fixed;
  justify-content: space-between;
  padding-top: 10px;
}
.netflix-photo {
  object-fit: contain;
  left: 10px;
  position: fixed;
  width: 80px;
}
.profile-photo {
  object-fit: contain;
  right: 10px;
  position: fixed;
  width: 30px;
}
.new-nav-bar {
  background-color: black;
}
